*, *::before, *::after {
  box-sizing: border-box;
}

body {
  background-color: #f5f5f5;
  font-family: 'Lato', "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 1rem;
}

.header {
  text-align: center;
  line-height: 1;
  margin-bottom: 2rem;
}

.header .title {
  margin: 0;
  font-size: 4.8rem;
  font-family: 'Megrim', cursive;
}

.header .sub-title {
  margin: 0;
  font-size: 1.3rem;
}

.chart-box {
  position: relative;
  height: 50vh;
}

@media (min-width: 500px) {
  .header .title {
    font-size: 7.8rem;
  }
  
  .header .sub-title {
    font-size: 2rem;
  }
}

@media (min-width: 1000px) {
  body {
    padding: 2rem;
  }
  
  .header .title {
    font-size: 16rem;
  }
  
  .header .sub-title {
    font-size: 2rem;
  }

  .chart-box {
    height: 70vh;
  }
}
